html {
 scroll-behavior: smooth;
}

body {
 min-width: 100vw;
 min-height: 100vh;
 overflow-x: hidden;

 color: $text-color-1;
 font-family: $main-font, $main-font-respaldo;

 background-color: $background-color-3;
}

h1,
h2,
h3 {
 margin: 0;
}

img {
 max-width: 100vw;
 height: auto;
}
